import { Component, OnInit } from '@angular/core';
import {QuestionService} from '../../../service/question.service';

@Component({
  selector: 'app-complete-card',
  templateUrl: './complete-card.component.html',
  styleUrls: ['./complete-card.component.css']
})
export class CompleteCardComponent implements OnInit {

  curUser = JSON.parse(localStorage.getItem('curUser'));
  userId = JSON.parse(localStorage.getItem('curUser')).id;
  scoreList = [0,0,0,0,0,0,0];
  totalScore = 0;
  sessions: any;
  sessionId: number;
  sessionNum: number;

  constructor(
    private questionService: QuestionService
  ) { }

  ngOnInit() {
    this.getSessions();
    this.questionService.getSessionByUser(this.userId).subscribe(value => {
          this.sessionId = value[0].id;
          console.log(this.sessionId);
          this.questionService.getScoreBySession(this.sessionId).subscribe( value => {
            this.scoreList = value;
            for(let i = 0; i < value.length; i++){
              this.totalScore += value[i];
            }
            console.log(this.totalScore);
            console.log(this.scoreList);
          })
      }
      )
    }
  

  getSessions() {
    this.questionService.getSessionByUser(this.curUser.id).subscribe(value => {
      this.sessions = value;
      this.sessionNum = value.length;
    })
  }

}
